/* ====================  FONTS  ==================== */

@mixin font-face($family, $src, $style: normal,$weight: normal) {
  @font-face {
    font-family: $family;
    src: url('#{$src}.eot');
    src: url('#{$src}.eot?#iefix') format('embedded-opentype'), url('#{$src}.woff2') format('woff2'), url('#{$src}.woff') format('woff'), url('#{$src}.ttf')  format('truetype'), url('#{$src}.svg##{$family}') format('svg');
    font-style: $style;
    font-weight: $weight; } }
