.loader {
  height: 100vh;
  width: 100vw;
  position: fixed;
  z-index: 99999;
  top: 0;
  background: #ffffff;
}

.loader__balls {
  margin: 0 auto;
  width: 70px;
  text-align: center;
  position: absolute;
  left: 0;
  right: 0;
  top: 50%;
}

.loader__ball {
  position: relative;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  display: inline-block;
  -webkit-animation: bouncedelay 3s infinite cubic-bezier(0.62, 0.28, 0.23, 0.99) both;
  animation: bouncedelay 3s infinite cubic-bezier(0.62, 0.28, 0.23, 0.99) both;
}

.loader__ball--1 {
  -webkit-animation-delay: -.16s;
  animation-delay: -.16s;
}

.loader__ball--2 {
  -webkit-animation-delay: -.08s;
  animation-delay: -.08s;
}

@keyframes bouncedelay {
  0% {
    bottom: 0;
    background-color: #79d1d6;
  }
  16.66% {
    bottom: 40px;
    background-color: #92d2a7;
  }
  33.33% {
    bottom: 0px;
    background-color: #7ebc02;
  }
  50% {
    bottom: 40px;
    background-color: #79d1d6;
  }
  66.66% {
    bottom: 0px;
    background-color: #92d2a7;
  }
  83.33% {
    bottom: 40px;
    background-color: #7ebc02;
  }
  100% {
    bottom: 0;
    background-color: #79d1d6;
  }
}

